@import "mixins";


body, html {
	height: 100%; }

body {
	font-family: 'Source Sans Pro', sans-serif !important;
	font-weight: 200;
	line-height: 1; }

body, h2, p {
	margin: 0; }

.top {
	width: 100%;
	height: 100px; }

// .carousel-cell:before,
// .carousel-cell__number
// 	display: none !important


.swiper-wrapper {
	// height: 200px
	// max-height: 100% !important
	height: 705px;
	// height: 100%
	.swiper-slide {
		// position: relative
 }		// @include transition-ease(0.7s)
	.swiper-slide-h {
		font-weight: 200;
		margin-bottom: 55px;
		font-size: 36px;
		letter-spacing: normal;
		position: relative; } }

.swiper-wrapper {
	.swiper-slide {
		// width: 100% !important
		// height: 100%
		padding: 80px;
		padding-left: 15%;
		padding-right: 15%; } }


.swiper-slide-container {
	position: relative;
	width: 90%;
	margin: auto;
	font-size: 0;
	letter-spacing: -1px;
	span {
		display: block !important; } }

.swiper-slide-text-block {
	div {
 }		// display: block !important
	vertical-align: top;
	// vertical-align: bottom

	letter-spacing: normal;
	position: relative !important;
	// width: 30%
	font-size: 18px;
	display: inline-block;
	.text-block:nth-child(2) {
		margin-bottom: 20px; }
	.text-block {
		max-width: 380px;
		margin-bottom: 30px;

		.text-block-text {
			line-height: 1 !important;
			// padding-right: 60px
			padding-right: 5%; } }
	.text-block-cms {
		margin-bottom: 20px;
		display: block; }
	.text-block-h {
		font-family: 'Source Sans Pro', sans-serif !important;
		font-weight: 400; }
	.swiper-slide-logo {
		margin-bottom: 50px;
		display: block; }
	.text-block-link {
		display: block;
		font-weight: 400;
		a {
			display: block;
			color: #2cd094;
			text-decoration: underline; } } }

.device-block {
	// vertical-align: top
	// width: 50%
	max-width: 705px;
	// height: 100%
	// float: right
	// position: absolute
	position: relative !important;
	// bottom: 0
	// right: 4%
	display: inline-block;
	// @include transition-ease(0.1s)
	.device-img {
		// width: 50%
		// position: fixed
		position: absolute;
		// float: right
		// position: relative
		bottom: 0px;
		display: inline-block;
		text-align: center;
		@include transition-ease(0.2s);
		img {
			width: 95%;
			vertical-align: middle;
			vertical-align: top; } }
	.device-block-desktop {
		position: relative;
		max-width: 660px;
		width: 105%;
		right: 0; }
	.device-block-tablet {
		right: 75%;
		left: -4%;
		max-width: 241px;
		width: 38%; }
	.device-block-mobile {
		right: 103%;
		left: -9%;
		max-width: 115px;
		width: 18%; } }


.swiper-wrapper {
	.swiper-slide-1 {
		background: #ffecb3; }
	.swiper-slide-2 {
		background: #29b4ef;
		color: $white;
		.text-block-link {
			a {
				color: $white; } } }
	.swiper-slide-3 {
		background: #4caf50;
		color: $white;
		.text-block-link {
			a {
				color: $white; } } }

	.swiper-slide-4 {
		background: #e1f5fe;
		color: #727272;
		.text-block-link {
			a {
				color: #727272; } } }

	.swiper-slide-5 {
		background: #f4f5f6;
		color: #727272;
		.text-block-link {
			a {
				color: #727272; } } } }
